body {
    background-color: #78AEFF;
}

.main {
    display: flex;
    width: 1000px;
    height: 500px;
    margin: 100px auto;
}

.main-box {
    display: flex;
    flex-direction: column;
    margin: auto
}

.try-label {
    width: 336px;
    height: 37px;
    background: #F00000;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.36);
    border-radius: 5px;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 14 px;
    letter-spacing: 0.01em;
    color: #FFFFFF;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
}

.subed {
    background-color: green;
    font-size: 13px;
}

.again {
    cursor: pointer;
    color: gray;
    font-size: 14px;
    margin-top: 5px;
    width: 90px;
}

.again:hover {
    color: black;
}

.margin {
    margin-top: 20px;
}

.main-area {
    width: 338px;
    height: 350px;
    background: #FFFFFF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 7px;
    margin-top: 40px;
}

input {
    margin: 10px;
}

.insert {
    width: 70%;
    height: 33px;
    background: #FFFFFF;
    border: 1px solid;
    border-radius: 2px;
    margin: 10px 25px;
    padding: 0 25px;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
}

.claim-button {
    width: 286px;
    height: 33px;
    background: #B5B5B5;
    box-shadow: 0px 1px 0px rgba(0, 0, 0, 0.46);
    border-radius: 2px;
    cursor: pointer;
    margin: 0 25px;
    border: none;
    font-weight: 700;
    font-size: 13px;
    line-height: 99.19%;
    color: white;
}

.claimed {
    background-color: green;
}

.terms-and-service {
    text-align: center;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 9px;
    line-height: 99.19%;
    letter-spacing: 0.053em;
    color: #C2C2C2;
    margin-top: 10px;
}

.skills {
    width: 85%;
    margin: 10px 25px;
}

.cross {
    margin-left: 15px;
}